import { useTranslation } from "react-i18next";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { 
  useAdminStats, 
  useTopRestaurants, 
  useActivityEngagement,
  useRestaurantRatingTrends,
  useRestaurantCommentTrends,
  useActivityShareTrends,
  useActivityShareStats
} from "@/hooks/useAdmin";
import { useUserRole } from "@/hooks/useUserRole";

import { Users, Store, Calendar, MessageSquare, TrendingUp, Star, UserPlus, BarChart3, Forward } from "lucide-react";
import {
  LineChart,
  Line,
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";

const AdminDashboard = () => {
  const { t } = useTranslation();
  const { isSupervisor } = useUserRole();
  const { data: stats, isLoading } = useAdminStats();
  const { data: topRestaurants } = useTopRestaurants();
  const { data: activityEngagement } = useActivityEngagement();
  const { data: ratingTrends } = useRestaurantRatingTrends();
  const { data: commentTrends } = useRestaurantCommentTrends();
  const { data: shareTrends } = useActivityShareTrends();
  const { data: shareStats } = useActivityShareStats();

  if (isLoading) {
    return <div className="text-center">{t("common.loading")}</div>;
  }

  return (
    <div className="space-y-6">
        <div>
          <h1 className="text-3xl font-bold mb-2">{t("admin.dashboard")}</h1>
          <p className="text-muted-foreground">{t("admin.dashboardDescription")}</p>
        </div>

        {/* Stats Cards */}
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">
                {isSupervisor ? t("admin.managedMerchants") : t("admin.totalUsers")}
              </CardTitle>
              <Users className="h-4 w-4 text-muted-foreground" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats?.totalUsers || 0}</div>
              <p className="text-xs text-muted-foreground">
                {isSupervisor ? t("admin.managedMerchantsDesc") : t("admin.registeredUsers")}
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">
                {isSupervisor ? t("admin.managedRestaurants") : t("admin.totalRestaurants")}
              </CardTitle>
              <Store className="h-4 w-4 text-muted-foreground" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats?.totalRestaurants || 0}</div>
              <p className="text-xs text-muted-foreground">
                {isSupervisor ? t("admin.managedRestaurantsDesc") : t("admin.listedRestaurants")}
              </p>
            </CardContent>
          </Card>

          {!isSupervisor && (
            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">{t("admin.totalActivities")}</CardTitle>
                <Calendar className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{stats?.totalActivities || 0}</div>
                <p className="text-xs text-muted-foreground">{t("admin.activeEvents")}</p>
              </CardContent>
            </Card>
          )}

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">
                {isSupervisor ? t("admin.restaurantComments") : t("admin.totalComments")}
              </CardTitle>
              <MessageSquare className="h-4 w-4 text-muted-foreground" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats?.totalComments || 0}</div>
              <p className="text-xs text-muted-foreground">
                {isSupervisor ? t("admin.restaurantCommentsDesc") : t("admin.userReviews")}
              </p>
            </CardContent>
          </Card>
        </div>

        {/* Charts Section */}
        <div className="grid gap-4 md:grid-cols-2">
          {/* Restaurant Rating Trends */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <TrendingUp className="h-5 w-5" />
                {t("admin.restaurantRatingTrends")}
              </CardTitle>
              <CardDescription>
                {isSupervisor ? t("admin.restaurantRatingTrendsDescSupervisor") : t("admin.restaurantRatingTrendsDescAdmin")}
              </CardDescription>
            </CardHeader>
            <CardContent>
              {ratingTrends && ratingTrends.length > 0 ? (
                <ResponsiveContainer width="100%" height={300}>
                  <LineChart data={ratingTrends}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="month" />
                    <YAxis domain={[0, 5]} />
                    <Tooltip />
                    <Legend />
                    {Object.keys(ratingTrends[0])
                      .filter(key => key !== "month")
                      .map((restaurantName, index) => (
                        <Line
                          key={restaurantName}
                          type="monotone"
                          dataKey={restaurantName}
                          stroke={`hsl(${(index * 360) / 5}, 70%, 50%)`}
                          strokeWidth={2}
                        />
                      ))}
                  </LineChart>
                </ResponsiveContainer>
              ) : (
                <div className="h-[300px] flex items-center justify-center text-muted-foreground">
                  {t("admin.noRatingData")}
                </div>
              )}
            </CardContent>
          </Card>

          {/* Restaurant Comment Count Trends */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <BarChart3 className="h-5 w-5" />
                {t("admin.restaurantCommentTrends")}
              </CardTitle>
              <CardDescription>
                {isSupervisor ? t("admin.restaurantCommentTrendsDescSupervisor") : t("admin.restaurantCommentTrendsDescAdmin")}
              </CardDescription>
            </CardHeader>
            <CardContent>
              {commentTrends && commentTrends.length > 0 ? (
                <ResponsiveContainer width="100%" height={300}>
                  <BarChart data={commentTrends}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="month" />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    {Object.keys(commentTrends[0])
                      .filter(key => key !== "month")
                      .map((restaurantName, index) => (
                        <Bar
                          key={restaurantName}
                          dataKey={restaurantName}
                          fill={`hsl(${(index * 360) / 5}, 70%, 50%)`}
                        />
                      ))}
                  </BarChart>
                </ResponsiveContainer>
              ) : (
                <div className="h-[300px] flex items-center justify-center text-muted-foreground">
                  {t("admin.noCommentData")}
                </div>
              )}
            </CardContent>
          </Card>

          {/* Activity Engagement Chart - Only for admins */}
          {!isSupervisor && activityEngagement && activityEngagement.length > 0 && (
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <UserPlus className="h-5 w-5" />
                  {t("admin.activityEngagement")}
                </CardTitle>
                <CardDescription>{t("admin.activityEngagementDescription")}</CardDescription>
              </CardHeader>
              <CardContent>
                <ResponsiveContainer width="100%" height={300}>
                  <BarChart data={activityEngagement}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="name" angle={-45} textAnchor="end" height={100} />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <Bar dataKey="subscribers" name={t("admin.subscribers")} fill="hsl(var(--primary))" />
                    <Bar dataKey="comments" name={t("admin.comments")} fill="hsl(var(--accent))" />
                  </BarChart>
                </ResponsiveContainer>
              </CardContent>
            </Card>
          )}

          {/* Activity Share Stats - Only for admins */}
          {!isSupervisor && (
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Forward className="h-5 w-5" />
                  {t("admin.activityShareRanking")}
                </CardTitle>
                <CardDescription>{t("admin.activityShareRankingDesc")}</CardDescription>
              </CardHeader>
              <CardContent>
                {shareStats && shareStats.length > 0 ? (
                  <ResponsiveContainer width="100%" height={300}>
                    <BarChart data={shareStats} layout="vertical">
                      <CartesianGrid strokeDasharray="3 3" />
                      <XAxis type="number" />
                      <YAxis dataKey="name" type="category" width={120} tick={{ fontSize: 12 }} />
                      <Tooltip />
                      <Bar dataKey="shares" name={t("admin.shareCount")} fill="hsl(var(--primary))" />
                    </BarChart>
                  </ResponsiveContainer>
                ) : (
                  <div className="h-[300px] flex items-center justify-center text-muted-foreground">
                    {t("admin.noShareData")}
                  </div>
                )}
              </CardContent>
            </Card>
          )}

          {/* Activity Share Trends - Only for admins */}
          {!isSupervisor && shareTrends && shareTrends.length > 0 && (
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <TrendingUp className="h-5 w-5" />
                  {t("admin.activityShareTrends")}
                </CardTitle>
                <CardDescription>{t("admin.activityShareTrendsDesc")}</CardDescription>
              </CardHeader>
              <CardContent>
                <ResponsiveContainer width="100%" height={300}>
                  <LineChart data={shareTrends}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="month" />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    {Object.keys(shareTrends[0])
                      .filter(key => key !== "month")
                      .map((activityName, index) => (
                        <Line
                          key={activityName}
                          type="monotone"
                          dataKey={activityName}
                          stroke={`hsl(${(index * 360) / 10}, 70%, 50%)`}
                          strokeWidth={2}
                        />
                      ))}
                  </LineChart>
                </ResponsiveContainer>
              </CardContent>
            </Card>
          )}
        </div>

        {/* Top Restaurants */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Star className="h-5 w-5" />
              {isSupervisor ? t("admin.topRestaurantsRanking") : t("admin.topRestaurants")}
            </CardTitle>
            <CardDescription>
              {isSupervisor ? t("admin.topRestaurantsRankingDescSupervisor") : t("admin.topRestaurantsDescription")}
            </CardDescription>
          </CardHeader>
          <CardContent>
            {topRestaurants && topRestaurants.length > 0 ? (
              <ResponsiveContainer width="100%" height={400}>
                <BarChart data={topRestaurants} layout="horizontal">
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis type="number" />
                  <YAxis dataKey="name" type="category" width={150} />
                  <Tooltip />
                  <Legend />
                  <Bar dataKey="rating" name={t("admin.rating")} fill="hsl(var(--primary))" />
                  <Bar dataKey="comments" name={t("admin.comments")} fill="hsl(var(--accent))" />
                </BarChart>
              </ResponsiveContainer>
            ) : (
              <div className="h-[400px] flex items-center justify-center text-muted-foreground">
              {isSupervisor ? t("admin.noAssignedRestaurants") : t("admin.noData")}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
};

export default AdminDashboard;
